.LoginBlock {
    width: 26.5%;
    height: 50%;
    display: inline-block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    box-shadow: black;
    border-radius: 10px;
    border: 1px solid black;
    background-color: white;
}

.logIn {
    height: 45%;
    width: 12%;
    font-size: 340%;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    margin: auto;
    display: inline-block;
    font-weight: 600;

}

.LoginContainer {
    display: inline-block;
    top: 0;
    bottom: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 26.5%;
    height: 50%;
    position: fixed;
}
.InputContainer {
    height: 20%;
    width: 20%;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    position: fixed;
    margin: auto;
    input {
        display: inline-block;
        width: 100%;
        height: 40px;
        border-radius: 10px;
        font-size: 200%;
        margin-bottom: 10px;
        resize: none;
    }
    button {
        display: inline-block;
        border-radius: 10px;
        width: 100%;
        height: 40px;
        margin-bottom: 10px;
        border: 0;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        font-weight: 600;
        color: white;
        background-color: brown;
    }
    button:hover {
        transition: all .3s linear;
        -webkit-transition: all .3s linear;
        -moz-transition: all .3s linear;
        background-color:red;
        color: white;
    }
}

.AnAccount {
    display: inline-block;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 600;
}

.checkBoxContainer {
    width: 400px;
    font-size: 20px;
    margin-bottom: 5px;
    margin-right: 15px;
}

.checkBoxContainer input {
    width: 20px;
    height: 20px;
}

.formSummaryError {
    color: red;
}
